import React, { useState } from 'react'
import Tab from 'react-bootstrap/Tab'
import 'bootstrap/dist/css/bootstrap.min.css';
import MisDatos from '../datos/MisDatos'
import { authenticationService } from '../_services';

const AppTabs = () => {
	const [key, setKey] = useState('home');
    return (
       <Tab.Content className="full">
        <Tab.Pane eventKey="home" title="Home" className="full">
          <div className="titlePage">Home</div>
			<div className="foto"><img src='img/jugar.jpg' /></div>
        </Tab.Pane>
        <Tab.Pane eventKey="datos" title="Mis Datos" className="full">
			<MisDatos className="full"/>
        </Tab.Pane>
     </Tab.Content>
    )
}

export default AppTabs;

